

















































import Vue from 'vue';
import { boardGameService } from '@/axios-service';
export default Vue.extend({
  name: 'GameList',
  data: () => ({
    games: null,
    error: ''
  }),
  created() {
    this.getGames();
  },
  methods: {
    async getGames() {
      try {
        this.games = (await boardGameService.get('games')).data;
      } catch (error) {
        this.error = error;
      }
    },
    deleteGame(id: number) {
      boardGameService.delete(`games/${id}`).then(response => {
        this.getGames();
      });
    }
  }
});
