



















import Vue from 'vue';
import StarRating from 'vue-star-rating';
import { boardGameService } from '@/axios-service';
import { User } from '@/models/User';
export default Vue.extend({
  name: 'GameSessionDetails',
  components: {
    StarRating
  },
  props: {
    id: {
      type: Number,
      required: true
    }
  },
  data: () => ({
    loading: true,
    headers: [
      {
        text: 'Player',
        value: 'player'
      },
      {
        text: 'Rating',
        value: 'rating'
      }
    ],
    session: {}
  }),
  methods: {
    async ratingChanged(rating: number, playerId: number) {
      try {
        await boardGameService.post(
          `rating/gameplay-session/${this.id}/player/${playerId}`,
          {
            rating: rating
          }
        );
      } catch (error) {}
    }
  },
  computed: {
    player(): User {
      return this.$store.state.user;
    }
  },
  async mounted() {
    this.loading = true;
    try {
      this.session = (await boardGameService.get(
        `game-session/${this.id}`
      )).data;
    } catch (error) {}
    this.loading = false;
  }
});
