






































import Vue from 'vue';
import axios from 'axios';
import _ from 'lodash';
import { boardGameService } from '../axios-service';
import router from '@/router';
export default Vue.extend({
  name: 'home',
  components: {},
  data: () => ({
    search: '',
    searching: false,
    searchError: '',
    boardGames: []
  }),
  methods: {
    slowSearch: _.debounce(async function(this: any) {
      this.fastSearch();
    }, 2000),
    async fastSearch() {
      this.searching = true;
      try {
        const result = await boardGameService.get(
          `games/search-import?search=${this.search}`
        );
        this.boardGames = result.data;
      } catch (e) {
        if (e.message === 'Network Error') {
          this.searchError = 'Network Error';
        }
      }
      this.searching = false;
    },
    importGame(objectid: number) {
      boardGameService
        .post('games', {
          objectid
        })
        .then(response => {
          router.push('games');
        });
    }
  },
  watch: {
    async search() {
      if (this.search.length > 2) {
        this.slowSearch();
      }
    }
  }
});
