








































































import Vue from 'vue';
export default Vue.extend({
  data: () => ({
    drawer: null
  }),
  props: {
    source: String
  },
  methods: {
    logout() {
      this.$store.commit('logout');
      this.$router.push({ name: 'login' });
    }
  },
  computed: {
    userName(): string {
      return this.$store.state.user.name;
    }
  }
});
